.join-us {
  padding: 4rem 4rem;
  background: #D1D1DC;
}

.select-profile {
  display: flex;
  padding: 2rem;
  text-align: center;
  margin: 2rem auto;
  background: #E6E6E8;
  box-shadow: 0px 4px 40px rgba(33, 32, 156, 0.1);
  border-radius: 25px;
}

.join-us figure img {
  width: 100%;
}

.join-us h1 {
  color: #5E5DBA;
  font-size: 2rem;
  margin: 1rem auto;
}

.join-us p {
  color: #5E5DBA;
  font-size: 1rem;
  font-weight: 500;
  margin: 1rem;
}

.join-us button {
  margin: 1.5rem auto;
  box-shadow: 0px 4px 20px rgba(4, 85, 143, 0.2);
  display: block;
  border: none;
  background: #5E5DBA;
  max-width: fit-content;
  font-size: 1rem;
  font-weight: bold;
  border-radius: 10px;
}

.links {
  color: white;
  padding: .7rem 1.5rem;
  display: inline-block;
  font-family: poppins;
}

@media only screen and (max-width:850px) {
  .join-us {
    padding: 1rem;
  }

  .select-profile {
    flex-direction: column;
  }
}