@import url('https://fonts.googleapis.com/css2?family=Overpass:wght@300;600&family=Poppins:wght@400;500;600&family=Ubuntu:wght@400;500;700&display=swap');
body {
  margin: 0;
  padding: 0;
  font-family:poppins,  "Overpass", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
a{
  text-decoration: none;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
html{
  scroll-behavior: smooth;
}
ul{
  list-style: none;
}
.links{
  text-decoration: none;
}
::selection{
  color: white;
  background: #5E5DBA;
}